:root {
  --gold-web-golden: #f9d624;
  --spanish-orange: #e65c00;
  --persian-green: #11978c;
  --granite-gray: #666;
  --spring-green: #39ef7f;
  --ultramarine: #4b00e0;
  --blue-violet: #8d2ce2;
  --smoky-black: #121212;
  --dark-orange: #ff8c00;
  --winter-sky: #ff057a;
  --cultured-1: #fafafa;
  --cultured-2: #f2f2f2;
  --cultured-3: #f0f0f0;
  --light-gray: #ccc;
  --alice-blue: #ddeffd;
  --white_50: #ffffff80;
  --white-1: #fff;
  --white-2: #fcfcfc;
  --black: #000;
  --ff-spartan: "League Spartan", sans-serif;
  --fs-1: 3rem;
  --fs-2: 2.7rem;
  --fs-3: 2.2rem;
  --fs-4: 1.8rem;
  --fs-5: 1.6rem;
  --fw-400: 400;
  --fw-500: 500;
  --fw-600: 600;
  --fw-700: 700;
  --fw-900: 900;
  --section-padding: 60px;
  --shadow-1: 0 2px 25px #0000001a;
  --shadow-2: 0 8px 14px #0000001a;
  --shadow-3: 0 14px 30px #0000000d;
  --radius-6: 6px;
  --radius-12: 12px;
  --gradient-1: linear-gradient(to right, var(--winter-sky), var(--dark-orange));
  --gradient-2: linear-gradient(to right, var(--ultramarine), var(--blue-violet));
  --gradient-3: linear-gradient(to right, var(--winter-sky), var(--dark-orange));
  --gradient-4: linear-gradient(to right, var(--ultramarine), var(--blue-violet));
  --gradient-5: linear-gradient(to right, var(--persian-green), var(--spring-green));
  --gradient-6: linear-gradient(to right, var(--spanish-orange), var(--gold-web-golden));
  --transition-1: .25s ease;
  --transition-2: .5s ease;
  --cubic-out: cubic-bezier(.33, .85, .4, .96);
}

*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

a, img, svg, span, input, button, strong, ion-icon {
  display: block;
}

img {
  height: auto;
}

input, button {
  font: inherit;
  background: none;
  border: none;
}

input {
  width: 100%;
}

button {
  cursor: pointer;
}

ion-icon {
  pointer-events: none;
}

html {
  font-family: var(--ff-spartan);
  scroll-behavior: smooth;
  font-size: 10px;
}

body {
  background-color: var(--white-1);
  color: var(--granite-gray);
  font-size: 1.6rem;
}

:focus-visible {
  outline-offset: 4px;
}

.container {
  padding-inline: 15px;
}

.section {
  padding-block: var(--section-padding);
}

.w-100 {
  width: 100%;
}

.h1, .h2, .h3 {
  color: var(--black);
  font-weight: var(--fw-900);
  line-height: 1.3;
}

.h1 {
  font-size: var(--fs-1);
}

.h2 {
  font-size: var(--fs-2);
}

.h3 {
  font-size: var(--fs-3);
  font-weight: var(--fw-600);
}

.section-text {
  line-height: 1.7;
}

.btn {
  min-height: 50px;
  color: var(--white-1);
  border-radius: var(--radius-6);
  padding-inline: 25px;
}

.btn-primary {
  background-image: var(--gradient-1);
}

.btn-secondary {
  background-image: var(--gradient-2);
}

.btn-link {
  transition: var(--transition-1);
  align-items: center;
  gap: 5px;
  display: flex;
}

.btn-link:is(:hover, :focus) {
  letter-spacing: .3px;
}

.header .btn {
  display: none;
}

.header {
  background-color: var(--white-1);
  z-index: 4;
  width: 100%;
  padding-block: 20px;
  position: absolute;
  top: 0;
  left: 0;
}

.header.active {
  box-shadow: var(--shadow-1);
  animation: slide-in .5s var(--cubic-out) forwards;
  position: fixed;
  top: -75px;
}

@keyframes slide-in {
  0% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(75px);
  }
}

.header > .container {
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  display: flex;
}

.logo {
  color: var(--smoky-black);
  font-size: 3.5rem;
  font-weight: var(--fw-700);
  line-height: 1;
}

.nav-toggle-btn {
  font-size: 30px;
}

.nav-toggle-btn.active .menu-icon, .nav-toggle-btn .close-icon {
  display: none;
}

.nav-toggle-btn .menu-icon, .nav-toggle-btn.active .close-icon {
  display: block;
}

.navbar {
  background-color: var(--white-1);
  width: calc(100% - 30px);
  box-shadow: var(--shadow-2);
  visibility: hidden;
  max-height: 0;
  transition: .25s var(--cubic-out);
  position: absolute;
  top: 100%;
  left: 50%;
  overflow: hidden;
  transform: translateX(-50%);
}

.navbar.active {
  visibility: visible;
  max-height: 300px;
  transition-duration: .35s;
}

.navbar-item:not(:last-child) {
  border-block-end: 1px solid var(--alice-blue);
}

.navbar-link {
  color: var(--black);
  font-size: var(--fs-4);
  font-weight: var(--fw-500);
  transition: var(--transition-1);
  padding: 16px;
}

.navbar-link:is(:hover, :focus) {
  color: var(--winter-sky);
}

.hero {
  background-color: var(--cultured-1);
  padding-block-start: calc(73px + var(--section-padding));
}

.hero-banner {
  background-color: var(--light-gray);
  aspect-ratio: 1 / .8;
}

.hero-title {
  margin-block: 30px 15px;
}

.hero-form {
  margin-block: 20px 30px;
}

.input-field {
  background-color: var(--cultured-3);
  border-radius: var(--radius-6);
  margin-block-end: 15px;
  padding: 20px 15px;
}

.input-field::placeholder {
  transition: var(--transition-1);
}

.input-field:focus::placeholder {
  opacity: 0;
}

.hero-list {
  flex-wrap: wrap;
  align-items: center;
  gap: 20px;
  display: flex;
}

.hero-item {
  align-items: center;
  gap: 5px;
  display: flex;
}

.service {
  background-color: var(--white-1);
}

.service :is(.section-title, .section-text) {
  text-align: center;
}

.service .section-text {
  margin-block: 10px 35px;
}

.service-list {
  gap: 25px;
  display: grid;
}

.service-card {
  border-radius: var(--radius-6);
  box-shadow: var(--shadow-1);
  overflow: hidden;
}

.service-card .card-banner {
  background-color: var(--light-gray);
  aspect-ratio: 1 / .47;
  justify-content: stretch;
  align-items: stretch;
  width: 100%;
  height: auto;
  min-height: 180px;
  margin: 0;
  padding: 0;
  display: flex;
  overflow: hidden;
}

.service-card .card-banner video {
  object-fit: cover;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  display: block;
}

.service-card .card-content {
  padding: 20px;
}

.service-card .card-title {
  transition: var(--transition-1);
}

.service-card .card-title:is(:hover, :focus) {
  color: var(--winter-sky);
}

.service-card .card-text {
  margin-block: 10px 20px;
  line-height: 1.8;
}

.service-card .btn-link {
  color: var(--ultramarine);
}

.features {
  background-color: var(--white-2);
}

.features .section-text {
  margin-block: 10px 15px;
}

.features-content {
  margin-block-end: 25px;
}

.features-item {
  background-color: var(--white-1);
  box-shadow: var(--shadow-3);
  border-radius: var(--radius-6);
  align-items: center;
  gap: 10px;
  padding: 15px;
  display: flex;
}

.features-item:not(:last-child) {
  margin-block-end: 15px;
}

.features-item .item-title {
  color: var(--black);
  font-size: var(--fs-5);
  font-weight: var(--fw-600);
}

.features-banner {
  background-color: var(--light-gray);
  border-radius: var(--radius-12);
  max-width: max-content;
  overflow: hidden;
}

.features-banner.one {
  aspect-ratio: 1 / .83;
  margin-block-end: 25px;
}

.features-banner.two {
  aspect-ratio: 1 / .71;
  margin-inline: auto;
}

.about {
  text-align: center;
}

.about .section-text:first-of-type {
  margin-block: 5px 30px;
}

.about-list {
  gap: 25px;
  display: grid;
}

.about-card {
  color: var(--white-1);
  border-radius: var(--radius-12);
  padding: 20px;
}

.about-card-1 {
  background-image: var(--gradient-3);
}

.about-card-2 {
  background-image: var(--gradient-4);
}

.about-card-3 {
  background-image: var(--gradient-5);
}

.about-card-4 {
  background-image: var(--gradient-6);
}

.about-card .card-banner {
  aspect-ratio: 1;
  background-color: var(--white_50);
  min-width: 150px;
  height: 150px;
  transition: var(--transition-2);
  border-radius: 50%;
  place-items: center;
  margin-inline: auto;
  display: grid;
}

.about-card:is(:hover, :focus-within) .card-banner {
  background-color: var(--white-1);
}

.about-card .card-title {
  color: var(--white-1);
  margin-block: 20px 10px;
}

.about-card .card-text {
  margin-block-end: 15px;
  line-height: 1.8;
}

.about .btn-link {
  justify-content: center;
}

.about .section-text:last-of-type {
  max-width: 50ch;
  margin-block-start: 25px;
  margin-inline: auto;
}

.about .section-text:last-of-type .btn-link {
  color: var(--blue-violet);
  display: inline-flex;
}

.about .section-text:last-of-type .btn-link:is(:hover, :focus) {
  color: var(--winter-sky);
  letter-spacing: 0;
}

.stats {
  background-color: var(--cultured-2);
}

.stats-banner {
  aspect-ratio: 1 / .78;
  max-width: max-content;
  margin-block-end: 25px;
  margin-inline: auto;
}

.stats-list {
  gap: 25px;
  display: grid;
}

.stats-item {
  padding-block: 5px;
  padding-inline-start: 15px;
  position: relative;
}

.stats-item:before {
  content: "";
  border-radius: 5px;
  width: 3px;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.stats-item.purple:before {
  background-color: var(--ultramarine);
}

.stats-item.red:before {
  background-color: var(--winter-sky);
}

.stats-item.green:before {
  background-color: var(--spring-green);
}

.stats-item.yellow:before {
  background-color: var(--dark-orange);
}

.stats-item .item-title {
  color: var(--black);
  font-size: 3.5rem;
  font-weight: var(--fw-900);
  margin-block-end: 5px;
}

.stats-item .item-title .span {
  color: var(--granite-gray);
  font-weight: var(--fw-400);
  font-size: var(--fs-4);
  display: inline-block;
}

.app-content {
  margin-block-end: 25px;
}

.app .section-text {
  margin-block: 5px 10px;
}

.app .btn-group {
  gap: 15px;
  display: flex;
}

.app .btn {
  min-height: auto;
  padding: 15px;
}

.app .btn img {
  margin-block-end: 10px;
}

.app .btn .strong {
  font-weight: var(--fw-600);
  margin-block-start: 5px;
}

.app-banner {
  aspect-ratio: 1 / .72;
  max-width: max-content;
  margin-inline: auto;
}

.support {
  background-image: var(--gradient-2);
  color: var(--white-1);
}

.support .section-title {
  color: var(--white-1);
}

.support .section-text {
  margin-block: 10px 15px;
}

.support .btn {
  place-items: center;
  max-width: max-content;
  display: grid;
}

.footer-top {
  background-color: var(--cultured-1);
}

.footer-top .container {
  gap: 25px;
  display: grid;
}

.footer .logo {
  margin-block-end: 25px;
}

.footer-img {
  max-width: max-content;
}

.footer-list-title {
  color: var(--black);
  font-size: var(--fs-3);
  font-weight: var(--fw-600);
  margin-block-end: 20px;
}

.footer-list > li:not(:last-child) {
  margin-block-end: 15px;
}

.footer-link {
  max-width: max-content;
  transition: var(--transition-1);
  position: relative;
}

.footer-link:after {
  content: "";
  background-color: var(--ultramarine);
  width: 20px;
  height: 2px;
  transition: var(--transition-1);
  opacity: 0;
  position: absolute;
  top: 6px;
  left: calc(100% + 10px);
}

.footer-link:is(:hover, :focus) {
  color: var(--ultramarine);
}

.footer-link:is(:hover, :focus):after {
  opacity: 1;
}

.footer-item {
  align-items: baseline;
  gap: 5px;
  display: flex;
}

.footer-item .span {
  color: var(--black);
}

.footer-item-link {
  color: var(--granite-gray);
  transition: var(--transition-1);
  display: inline-block;
}

.footer-item-link:is(:hover, :focus) {
  color: var(--ultramarine);
}

.footer-bottom {
  text-align: center;
  padding-block: 25px;
  line-height: 1.3;
}

.copyright {
  margin-block-end: 15px;
}

.copyright .span {
  color: var(--winter-sky);
  font-weight: var(--fw-500);
  display: inline-block;
}

.copyright-link {
  font-weight: var(--fw-600);
  background-image: var(--gradient-3);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  display: inline-block;
}

.footer-bottom-list {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 5px 20px;
  display: flex;
}

.footer-bottom-link {
  transition: var(--transition-1);
}

.footer-bottom-link:is(:hover, :focus) {
  color: var(--ultramarine);
}

.back-top-btn {
  background-color: var(--white-1);
  color: var(--black);
  border: 1px solid var(--light-gray);
  opacity: 0;
  visibility: hidden;
  transition: var(--transition-1);
  z-index: 4;
  border-radius: 50%;
  padding: 12px;
  font-size: 2.2rem;
  position: fixed;
  bottom: 10px;
  right: 20px;
}

.back-top-btn.active {
  opacity: 1;
  visibility: visible;
  transform: translateY(-10px);
}

@media (width >= 480px) {
  .header .btn {
    min-height: 40px;
    margin-inline-start: auto;
    padding-inline: 15px;
    display: block;
  }
}

@media (width >= 575px) {
  .container {
    max-width: 540px;
    margin-inline: auto;
  }

  .hero-form {
    position: relative;
  }

  .hero-form .btn {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
  }

  .service-list, .about-list, .stats-list, .footer-top .container {
    grid-template-columns: 1fr 1fr;
  }

  .about-card {
    min-height: 100%;
  }

  .app .btn {
    align-items: center;
    gap: 15px;
    padding-inline: 25px;
    display: flex;
  }

  .app .btn img {
    margin-block-end: 0;
  }
}

@media (width >= 768px) {
  body {
    font-size: 2rem;
  }

  :root {
    --fs-1: 5rem;
    --fs-2: 3.5rem;
    --fs-3: 2.5rem;
  }

  .container {
    max-width: 720px;
  }

  .header .btn {
    font-size: 1.7rem;
    font-weight: var(--fw-500);
    min-height: 45px;
    padding-inline: 25px;
  }

  .service-card {
    --radius-6: 16px;
  }

  .service-card .card-content {
    padding: 30px;
  }

  .features-item .item-title {
    --fs-5: 2rem;
  }

  .features-banner.two {
    margin-block-start: -180px;
    margin-inline: auto 0;
  }

  .stats-list {
    padding-inline: 60px;
  }

  .stats-item {
    padding-inline-start: 20px;
  }

  .stats-item .item-title {
    font-size: 4rem;
  }

  .app .btn {
    padding: 20px 30px;
  }

  .app-content .span {
    font-size: 1.5rem;
  }

  .app-content .strong {
    font-size: 2rem;
  }
}

@media (width >= 992px) {
  :root {
    --fs-2: 4rem;
    --fs-3: 2.9rem;
    --section-padding: 100px;
  }

  .container {
    max-width: 960px;
  }

  :is(.service, .about, .app) .section-text {
    max-width: 55ch;
    margin-inline: auto;
  }

  .nav-toggle-btn {
    display: none;
  }

  .navbar, .navbar.active {
    all: unset;
  }

  .navbar-list {
    gap: 5px;
    display: flex;
  }

  .navbar-item:not(:last-child) {
    border-block-end: none;
  }

  .navbar-link {
    --fs-4: 2rem;
  }

  .header .btn {
    min-height: 50px;
    margin-inline-start: 0;
    padding-inline: 40px;
  }

  .hero .container {
    grid-template-columns: 1fr 1fr;
    align-items: center;
    gap: 50px;
    display: grid;
  }

  .hero-title {
    margin-block-start: 0;
  }

  .service-list {
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
  }

  .features .container {
    grid-template-columns: 1fr 1fr;
    align-items: center;
    gap: 25px;
    display: grid;
  }

  .features-content {
    margin-block-end: 0;
  }

  .features-item {
    padding: 20px;
  }

  .features-item:not(:last-child) {
    margin-block-end: 20px;
  }

  .features-item .item-title {
    --fs-5: 2.4rem;
  }

  .about-list {
    gap: 30px;
  }

  .about-card {
    padding: 40px;
  }

  .stats .container {
    grid-template-columns: 1fr 1fr;
    align-items: flex-start;
    display: grid;
  }

  .stats-banner {
    margin-block-end: 0;
  }

  .stats-list {
    grid-template-columns: 1fr;
  }

  .stats-item:nth-child(2n) {
    margin-inline-start: auto;
  }

  .stats-item .item-title {
    font-size: 4.5rem;
  }

  .stats-text {
    font-size: 2.2rem;
  }

  .app :is(.section-title, .section-text) {
    text-align: center;
  }

  .app .btn-group {
    justify-content: center;
  }

  .support .container {
    grid-template-columns: 1fr .5fr;
    place-items: center self-end;
    display: grid;
  }

  .footer .container {
    grid-template-columns: 1fr .8fr .8fr 1fr;
  }

  .footer-list-title {
    margin-block-end: 30px;
  }

  .footer-list > li:not(:last-child) {
    margin-block-end: 25px;
  }

  .footer-bottom {
    padding-block: 30px;
  }

  .footer-bottom .container {
    justify-content: space-between;
    align-items: center;
    display: flex;
  }

  .copyright {
    margin-block-end: 0;
  }
}

@media (width >= 1200px) {
  :root {
    --fs-1: 6rem;
    --fs-2: 5rem;
  }

  .container {
    max-width: 1140px;
  }

  .features .container {
    grid-template-columns: 1fr max-content;
    gap: 70px;
  }

  .about-card {
    text-align: left;
    align-items: center;
    gap: 30px;
    display: flex;
  }

  .about-card .card-title {
    margin-block-start: 0;
  }

  .about .btn-link {
    justify-content: flex-start;
  }

  .app .container {
    grid-template-columns: .7fr 1fr;
    align-items: center;
    gap: 25px;
    display: grid;
  }

  .app-content {
    margin-block-end: 0;
  }

  .app :is(.section-title, .section-text) {
    text-align: left;
  }

  .app .section-title {
    --fs-2: 4rem;
  }

  .app .btn-group {
    justify-content: flex-start;
  }
}
